* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  background: #ffd8d0;
  position: relative;
}

a {
  display: inline-block;
  text-decoration: none;
  color: #000;
}

ul,
li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

button {
  background: none;
  border: none;
  cursor: pointer;
}

.container {
  padding: 0 10%;
}

section {
  width: 100%;
}

header {
  width: 100%;
  height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  border-bottom: 2px solid #000;
  display: flex;
  align-items: center;
}
header nav {
  width: 100%;
  height: inherit;
  display: flex;
  align-items: center;
}
header nav .logo-image__wrapper {
  display: flex;
  align-items: center;
  border-right: 2px solid #000;
  height: inherit;
}
header nav .logo-image__wrapper .logo-image {
  width: 120px;
  margin-right: 100px;
}
header nav .navbar {
  display: flex;
  margin-left: 100px;
  justify-content: space-between;
}
header nav .navbar .menu {
  display: flex;
}
header nav .navbar .menu li {
  padding: 0 30px;
  border-right: 2px solid #000;
}
header nav .navbar .menu li:nth-of-type(4) {
  border-right: none;
}
header nav .navbar .utility {
  display: flex;
}

#hero {
  margin-bottom: 250px;
}
#hero .wrapper {
  margin-top: 300px;
}
#hero .wrapper .main-text {
  font-size: 60px;
  font-weight: bold;
  letter-spacing: 1.5px;
  line-height: 1.25;
  margin-bottom: 40px;
}
#hero .wrapper .main-text span {
  display: block;
}
#hero .wrapper .main-text span:nth-of-type(3) {
  color: #fe665e;
}
#hero .wrapper .sub-text {
  color: #808080;
  margin-bottom: 100px;
}
#hero .wrapper .sub-text span {
  display: block;
}
#hero .wrapper .view-btn {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 15px 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background-color: #000;
  font-weight: 300;
}
#hero .wrapper .view-btn i {
  margin-left: 30px;
}

#products {
  margin-bottom: 300px;
}
#products .wrapper {
  width: 100%;
}
#products .wrapper .upper-box {
  width: inherit;
  border-top: 2px solid #000;
  height: 80px;
}
#products .wrapper .upper-box .left-column {
  width: 300px;
  height: inherit;
  border-right: 2px solid #000;
  display: flex;
}
#products .wrapper .upper-box .left-column .colored-box {
  width: 80px;
  height: 78px;
  background-color: #fe665e;
}
#products .wrapper .upper-box .left-column .section-number-box {
  width: 220px;
  height: inherit;
  display: flex;
  align-items: center;
  padding-left: 100px;
  font-size: 20px;
}
#products .wrapper .upper-box .left-column .section-number-box span {
  letter-spacing: 2.5px;
  font-weight: bold;
}
#products .wrapper .upper-box .left-column .section-number-box span:nth-of-type(2) {
  color: #808080;
}
#products .wrapper .middle-box {
  width: inherit;
  height: 500px;
  border-top: 2px solid #000;
  display: flex;
}
#products .wrapper .middle-box .left-column {
  width: 300px;
  height: inherit;
  border-right: 2px solid #000;
  display: flex;
  justify-content: center;
  text-align: center;
  padding-top: 80px;
}
#products .wrapper .middle-box .left-column span {
  color: #808080;
  font-weight: 300;
  display: block;
}
#products .wrapper .middle-box .right-column {
  width: calc(100% - 300px);
  padding-top: 80px;
  padding-left: 80px;
}
#products .wrapper .middle-box .right-column h1 {
  font-size: 80px;
  letter-spacing: 0;
}
#products .wrapper .bottom-box {
  width: 600px;
  height: 600px;
}
#products .wrapper .bottom-box .image-wrapper {
  width: 100%;
  height: inherit;
  border-top: 2px solid #000;
  border-right: 2px solid #000;
  border-bottom: 2px solid #000;
  display: flex;
}
#products .wrapper .bottom-box .image-wrapper .colored-box {
  width: 80px;
  height: 596px;
  background-color: #fe665e;
}
#products .wrapper .bottom-box .image-wrapper .section-image-wrapper {
  width: calc(100% - 80px);
  height: 596px;
}
#products .wrapper .bottom-box .image-wrapper .section-image-wrapper .section-image {
  width: 100%;
  height: 100%;
}